@import 'vars';

.main-slider .main-slider__pagination-container {
    width: 1200px;
    margin: auto;
    position: absolute;
    bottom: 20px;
    z-index: 1;
    .main-swiper__img {
        display: block;
        margin: auto;
    }
    .main-slider__pagination {
        float: right;
    }
}


.page-title {
    margin: 30px 0 30px 0;
    text-transform: none;
}

.page-about__information {
    padding: 40px 245px 75px;
    background-color: #fff;
    .page-title {
        margin: 0px 0 30px 0;
        padding-left: 0;
        text-transform: none;
    }
    p {
        line-height: 24px;
    }
}

.our-photos {
    .gallery-grid {
        height: 400px;      
    }
}

.questions {
    padding: 30px 245px 45px;
    text-align: center;
    background-color: #fff;
    .page-title {
        margin: 0 0 25px;
    }
    p {
        color: map_get($gray, default);
        line-height: 24px;
        margin: 0;
    }
    .see-all-link {
        margin-top: 20px;
    }
}

.subscribe {
    padding: 50px 50px 60px 80px;
    background-color: #ffffff;
    text-align: center;
    .block-title {
        margin-bottom: 65px;
    }
}